<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <div>
        <div class="container">
            <label>Fichier
              <input type="file" id="file" ref="file" v-on:change="handleFileUpload()"/>
            </label>
              <button v-on:click="submitFile()">Soumettre</button>
        </div>
    </div>
    <Image id ="bigimg" v-if="showImg" :callback="showGallery" :callback2="callCopyImage" :callback3="deleteImageSelected" ref ="childImg" />
    <Gallery id= "gal" v-else :callback="callImage" ref ="childGallery" />
  </div>
</template>
<script>
import Gallery from '@/components/Gallery.vue'
import Image from '@/components/Image.vue'
import { callRestServiceCopyImg, callRestServicePostImg, callRestServiceDeleteImg, callRestServiceGetImg } from '../http-api'

export default {
  name: 'Main',
  components: {
    Gallery,
    Image
  },
  props: {
    msg: String
  },
  data () {
    return {
      showImg: false,
      response: [],
      file: '',
      imageDataUrl: '',
      selected: 0,
      errors: []
    }
  },
  computed: {
    image: function () {
      return this.imageDataUrl
    }
  },
  methods: {
    handleFileUpload () {
      this.file = this.$refs.file.files[0]
    },
    callbackSubmitFile () {
      this.$refs.childGallery.refresh()
    },
    submitFile () {
      const formData = new FormData()
      formData.append('file', this.file)
      callRestServicePostImg(formData, this.callbackSubmitFile)
    },
    deleteImageSelected () {
      this.showGallery()
      const imageUrl = '/images/' + this.selected
      callRestServiceDeleteImg(imageUrl)
    },
    showGallery () {
      this.showImg = false
    },
    updateImage (data) {
      this.imageDataUrl = data.result
      this.$refs.childImg.setImg(this.imageDataUrl)
    },
    callCopyImage () {
      callRestServiceCopyImg(this.selected, this.callImage)
    },
    callImage (image) {
      const imageUrl = '/images/' + image.id
      this.selected = image.id
      this.showImg = true
      callRestServiceGetImg(imageUrl, this.updateImage, { })
    }
  }
}
</script>

