    <button v-on:click="callback()">Changer l'image</button>
    <button v-on:click="callback2()">Copier l'image</button>
    <button v-on:click="callback3()">Supprimer l'image</button>
    <section>
        <img id="imgDownloaded" />
    </section>
  </div>
</template>
<script>

export default {
  name: 'Image',
  components: {
  },
  props: {
