    callback2: Function,
    callback3: Function
  },
  data () {
    return {}
  },
  methods: {
    setImg (image) {
      const width = image.width
      const height = image.height
      document.querySelector('#imgDownloaded').setAttribute('src', image)
      document.querySelector('#imgDownloaded').setAttribute('height', 500)
      document.querySelector('#imgDownloaded').setAttribute('width', 500 * width / height)
    }

  }
}
</script>

