import axios from 'axios'

function callRestServiceGetLstImg (errorsTab, callback) {
  axios
    .get('images')
    .then((response) => {
    // JSON responses are automatically parsed.
      callback(response.data)
    })
    .catch((e) => {
      errorsTab.push(e)
    })
}

function callRestServiceGetImg (url, callback, data) {
  axios.get(url, { responseType: 'blob' })
    .then((response) => {
      var reader = new window.FileReader()
      reader.readAsDataURL(response.data)
      reader.onload = function () {
        data.result = reader.result
        callback(data)
      }
    })
}

function callRestServiceDeleteImg (url) {
  axios.delete(url)
}

function callRestServicePostImg (data, callback) {
  axios.post('/images',
    data
  ).then(function () {
    callback()
  })
}

function callRestServiceCopyImg (id, callback) {
  const imageUrl = '/images/'+id
  console.log(imageUrl)
  axios.put(imageUrl, {type: 'recreate'})
    .then((response) => {
      // JSON responses are automatically parsed.
        callback(response.data)
      })

}

export { callRestServiceCopyImg, callRestServicePostImg, callRestServiceDeleteImg, callRestServiceGetLstImg, callRestServiceGetImg }
